.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#but{
  
    margin: 20px;
    border-radius: 5px;
    height: 35px;
    width: 100px;
    font-weight: 700;
    font-family: "Segoe UI","Tahoma","Geneva","Verdana",sans-serif;
    cursor: pointer;
    border: 0;
}

#author {
  margin: 20px;
  font-style: italic;
}

#quote {
  margin: 20px;
  text-align: left;
}

#box {
    height: auto;
    width: 100%;
    border: 1px solid #917e7e;
    border-radius: 10px;
    text-align: right;
    max-width: 35rem;
    margin: 100px auto 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
